html body .modula-block-preview {
	background: #f3f3f5;
	border-radius: 6px;
	position: relative;
	overflow: hidden;
	height: 250px;

	.components-spinner {
		display: block;
		float: none;
		margin: 0 auto;
	}

	.modula-block-preview__content {
		width: 100%;
		max-width: 400px;
		margin: 40px auto;
		text-align: center;

		select.components-select-control__input {
			min-height: 40px;
			line-height: 20px;
		}
	}

	.modula-gallery-input {
		text-align: left;

		.selectize-dropdown-content {
			max-height: 100px;
		}

		.selectize-input::after {
			content: "";
			border: 0;
		}
	}
}

.modula-block-preview__logo {
	width: 100%;
	height: 60px;
	background-size: 75%;
}

.modula-block-preview--images {
	display: flex;
	flex-wrap: wrap;
	margin-left: -1%;
	margin-right: -1%;
}

.modula-preview-image-wrap {
	width: 23%;
	height: 0;
	padding-bottom: 23%;
	position: relative;
	margin: 1%;

	img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.modula-appender-media {
	height: 60px;
}

.modula-preview-image-wrap {
	width: 31%;
	padding-bottom: 23%;
	position: relative;
	margin: 1%;
	height: 200px;
}

.modula-preview-image-wrap .modula-button {
	width: 40px;
}

.modula-items {
	width: 100%;
}

.modula-item-content img.pic {
	height: auto;
}

.modula-button-group {
	display: flex;
	flex-direction: column;
	width: fit-content;
	margin: 0 auto;
}

.modula-button {
	margin: 0;
	background: white;
	color: black !important;
	height: 50px;
	font-size: 14px;
	box-shadow: none;
	border: 1px solid #e3e5e5;
	position: relative;
	width: calc(100% + 20px);

	svg {
		position: absolute;
		right: 5px;
	}
}

.modula-button:hover {
	color: #478db6 !important;
	text-decoration: underline !important;
}

html body .modula-button-group .modula-button-upsell:hover {
	text-decoration: underline;
	color: black !important;
}

html body .modula-button-group .modula-button-upsell {
	margin: 0;
	background: #3fbf90;
	color: black;
	height: 50px;
	font-size: 14px;
	width: calc(100% + 20px);
}
